import React, { Component , Fragment} from 'react';
import Header from '../components/Header/Header';
import Main from '../components/Main/Main';
import {Route,Switch} from 'react-router-dom';
import Soon from '../components/Soon/Soon';

 class Container extends Component {
  
  


  render() {
    console.log(this.props);
    return (
    
      <Fragment>
        <Header />
        <Switch>
        <Route path='/atech-web/' exact component={Main} />
        <Route path='/atech-web/ui' component={Soon} />
        <Route path='/atech-web/brandig' component={Soon} />
        <Route path='/atech-web/print' component={Soon} />
        <Route path='/atech-web/3d desing' component={Soon} />
        <Route path='/atech-web/book desing' component={Soon} />
        <Route path='/atech-web/product desing' component={Soon} />
        </Switch>
       
        
      </Fragment>
      
    )
  }
}
 export default Container;